.header {
    position: relative;
    // .header__top

    &__top {
        display: none;

        @media (min-width: 1025px) {
            display: block;
            padding: 1.2rem 0;
        }
    }

    // .header__top-inner

    &__top-inner {
        @extend %flex-row-center;
    }

    &__region {
        //min-width: 26.1rem;

        &:not(:last-child) {
            @media (max-width: 1024px) {
                margin-bottom: 3rem;
            }
        }
    }

    // .header__shops-search

    &__shops-search {
        @extend .header__action;

        @media (min-width: 1025px) {
            margin-right: auto;
        }

        &:not(:first-child) {
            @media (min-width: 1025px) {
                margin-left: 4.3rem;
            }
        }
    }

    // .header__contact

    &__contact {
        @media (min-width: 1025px) {
            display: flex;
        }
    }

    // .header__call

    &__call {
        @extend %vertical-align;

        &:not(:last-child) {
            @media (min-width: 1025px) {
                margin-right: 3.6rem;
            }

            @media (max-width: 1024px) {
                margin-bottom: 2.4rem;
            }
        }

        svg {
            fill: var(--dark);
        }
    }

    // .header__call-link

    &__call-link {
        @extend .header__action;

        &:not(:last-child) {
            margin-right: 0.6rem;
        }
    }

    // .header__whatsup

    &__whatsup {
        @extend .header__action;
        color: #4caf50;

        @media (any-hover: hover) {
            &:hover {
                color: #4caf50;
            }
        }
    }

    &__whatsup-icon {
        width: 1.6rem;
        height: 1.6rem;
    }

    // .header__main

    &__main {
        @extend %text-16;
        padding: 2.5rem 0 1rem;

        @media (max-width: 1024px) {
            padding-top: 2rem;
            padding-bottom: 0;
        }
    }

    // .header__main-top

    &__main-top {
        @extend %flex-row-center;
        flex-wrap: wrap;

        > * {
            @media (min-width: 1025px) {
                width: 33.3333%;
            }
        }

        @media (min-width: 1025px) {
            margin-bottom: 2.3rem;
        }
    }

    // .header__nav

    &__nav {
        display: flex;

        @media (max-width: 1024px) {
            width: 100%;
            order: 4;
            margin-top: 2.4rem;
        }
    }

    // .header__logo

    &__logo {
        @media (max-width: 1024px) {
            margin-left: 1.6rem;
            margin-right: auto;
        }
    }

    &__logo-img {
        width: 20.7rem;

        @media (max-width: 1024px) {
            width: 17rem;
            height: 2.5rem;
        }

        @media (max-width: 767px) {
            width: 12rem;
            height: 1.7rem;
        }
    }

    // .header__actions

    &__actions {
        display: flex;
        gap: 5.3rem;

        @media (max-width: 1024px) {
            gap: 2rem;
        }

        @media (min-width: 1025px) {
            justify-content: flex-end;
        }
    }

    // .header__action

    &__action {
        @extend %text-16;
        @extend %vertical-align;
        font-weight: 600;
        transition: $transition color;

        svg {
            width: 1.6rem;
            height: 1.6rem;
            margin-right: 0.6rem;
            fill: var(--dark);
            transition: $transition fill;

            @media (max-width: 1024px) {
                width: 2rem;
                height: 2rem;
            }

            @media (max-width: 1024px) {
                margin-right: 0;
            }
        }

        span {
            @media (max-width: 1024px) {
                display: none;
            }
        }

        img {
            flex-shrink: 0;
            margin-right: 0.6rem;
        }

        @media (any-hover: hover) {
            &:hover {
                color: var(--gray);

                svg {
                    fill: var(--gray);
                }
            }
        }
    }

    // .header__search-btn

    &__search-btn {
        display: none;
        @extend %bg-contain;

        @media (max-width: 1024px) {
            display: block;
            width: 2rem;
            height: 2rem;
            background-image: url(../images/icons/search.svg);
        }
    }

    // .header__main-bottom

    &__main-bottom {
        @media (min-width: 1025px) {
            position: relative;
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
        }

        @media (max-width: 1024px) {
            position: absolute;
            left: -100%;
            bottom: 0;
            transition: 0.5s left;
        }

        &.active {
            @media (max-width: 1024px) {
                left: 1.6rem;
                right: 1.6rem;
            }
        }
    }

    // .header__list

    &__list {
        display: none;

        @media (min-width: 1025px) {
            display: flex;
            gap: 3rem;
        }
    }

    // .header__list-item

    &__list-item {
        @media (any-hover: hover) {
            &:hover {
                .drop-menu {
                    @media (min-width: 1025px) {
                        visibility: visible;
                        opacity: 1;
                        pointer-events: all;
                    }
                }
            }
        }
    }

    // .header__list-link

    &__list-link {
        &--active {
            font-weight: 600;
        }

        &--red {
            color: var(--red);

            @media (any-hover: hover) {
                &:hover {
                    color: var(--red);
                }
            }
        }
    }
}

.region-header {
    display: inline-flex;
    position: relative;

    &__title {
        //color: #505661;

        &:not(:last-child) {
            margin-right: 0.6rem;
        }
    }

    // .region-header__value

    &__value {
        display: inline-block;
        padding-right: 2rem;
        font-weight: 600;
        cursor: pointer;
        transition: $transition color;

        @media (any-hover: hover) {
            &:hover {
                color: var(--gray);
            }
        }

        &:after {
            content: '';
            position: absolute;
            top: 0.3rem;
            right: 0;
            width: 1.4rem;
            height: 1.5rem;
            background-image: url(../images/icons/drop.svg);
            @extend %bg-contain;
            transition: $transition transform;
        }

        &.active {
            &:after {
                transform: rotate(180deg);
            }
        }
    }

    // .region-header__drop

    &__drop {
        display: none;
        position: absolute;
        top: calc(100% + 1.4rem);
        left: 50%;
        z-index: 20;
        background-color: #fff;
        box-shadow: 0rem -0.1rem 1rem rgba(0, 0, 0, 0.1);
        width: 24.7rem;
        padding: 2rem 1.5rem;

        @media (max-width: 500px) {
            width: 100%;
            left: 0;
        }
    }

    // .region-header__drop-item

    &__drop-item {
        cursor: pointer;
        transition: $transition color;

        @media (any-hover: hover) {
            &:hover {
                color: var(--gray);
            }
        }

        //white-space: nowrap;

        &:not(:last-child) {
            margin-bottom: 1.7rem;
        }

        &.active {
            font-weight: 600;
        }
    }
}
